
  import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Alert from 'vux/src/components/alert/index.vue'


  var readyToPay = function(conf,signature,orderPay,prepayId){
        var _self = this ;
        conf.success = function(res){
            //alert(res.err_msg);
            if (res.errMsg == "chooseWXPay:ok") {
                try{
                    _self.H.toAjax(true).then(function (data) { //data 
                        var submitData = data;
                        var checkpay = function (){
                            console.log(1111);
                            submitData.data = {
                                prepayId: prepayId
                            };
                            _self.A.post("/v1/orders/payCheck", submitData).then(function (result) {
                                console.warn("");
                                console.warn(result.data.data);
                                if (result.data.state.code == 0 && result.data.data.isPaySuccess && result.data.data.qrCode) {
                                   _self.extralReward = "";
                                   _self.showReward = false;
                                    _self.$emit("on-hide");
                                }else if(result.data.state.code == 0 && !result.data.data.isPaySuccess){
                                    setTimeout(checkpay,3000);
                                }else{
                                   alert(result.data.state.msg);//paycheck error
                                }
                            });
                        }
                        checkpay() ; //

                    });
                }catch(err){
                    _self.A.get("/v1/wechat/log?content="+err,{}).then(function(){})
                }


            } else if (res.errMsg == "chooseWXPay:cancel") {
                //"",
                alert("");

            } else if (res.errMsg == "chooseWXPay:fail") {

                //"",
                 _self.$doQrPay();

            };
        } ;

        var appId = conf.appId
        this.$wechat.config({
            debug: false, // ,apialertpclogpc
            appId: appId, // 
            timestamp: conf.timeStamp, // 
            nonceStr: conf.nonceStr, // 
            signature: signature,// 1
            jsApiList: ["chooseWXPay"] // JSJS2
        })

        conf.timestamp = conf.timeStamp ;
        delete conf.timeStamp ;
        var self = this;
        this.$wechat.ready(function(){
            self.$wechat.chooseWXPay(conf);
            self.$wechat.checkJsApi({
                jsApiList: ['chooseWXPay'], // JSJS2,
                success: conf.success
            });

        })
        this.$wechat.error(function(res){
            console.log(res);
//       alert(111,res);

            // configerrorconfigdebugresSPA
        });
    }

  export default {
    props: ['rewardData',"toUserId","momentId"],
    data(){
      return {
        baseUrl : M.baseImgUrl,
        showReward : false,
        viewControl: {      //,,,
          selectMoneyArr: [],
          curSelectMoneyIndex: ''
        },
       // rewardUserList :[], //
        cIndex : -1,
        extralReward : "",         money:"",

        show1 :false,
      }
    },
    methods: {
      toshowReward(){ // show 
        this.showReward = true ;
        this.$emit("on-show");
      },
      $hideReward(){
          this.$emit("on-hide");
      },
      checkMoneyItem(item, index){
        console.log(item, index);
        this.cIndex = index ;
        this.money = item ;
        this.extralReward = ""; //
      },
      rewardChange(val){
         this.cIndex = -1;
      },
      $blur_reward(e){

        if(e.target.value.length>7)this.extralReward = e.target.value =  e.target.value.slice(0,7);
        var val = parseFloat(e.target.value) ;
        var _self = this ;
        console.log(/^\d{0,4}(\.\d{2})?$/.test(val) , val >= 1 , val <= 3000 );
        if(/^\d{0,4}(\.\d{0,2})?$/.test(val) && val >= 1 && val <= 3000  ){

        }else{
          if(e.target.value.trim() == "" ){
            this.extralReward = "" ;
            e.target.value ="";  return ;
          }

          this.show1 = true ;
          this.extralReward = "" ;
          e.target.value ="";
        }

      },
      orderPay(){
        var money,prepayId ;
        var _self = this;
        if( this.extralReward  && parseFloat(this.extralReward) >=1 && /^\d{0,4}(\.\d{0,2})?$/.test(parseFloat(this.extralReward))  ){
          money = parseFloat(this.extralReward) *100 ;

        }else if(parseFloat(this.money) >= 100 && parseFloat(this.money) < 300000  && /^(\d{1,6})$/.test(parseFloat(this.money)) ){
          money = parseFloat(this.money) ;
        }else{
          this.show1 = true;
          this.extralReward = "";
          return ;
        }
        var orderPayObj = {
          momentId : this.rewardData.momentId,
          money: money,
          payType : "js",
          toUserId: this.rewardData.userId
        }
        this.H.toAjax(true).then(function (data) { //data 
          var submitData = data;
          var config = {
              appId: M.appId,
              timeStamp : (parseInt((new Date().getTime())/1000)).toString(),
              nonceStr : M.F.getId(16),
              package : "prepay_id=",

          }
          var goodsType = 1 ;
          var orderPay  = orderPayObj ;
          console.log(_self);
          var l = window.location ;
          var url = l.origin + "/admin/punchShow/meet" + l.search ;
          //var url = window.location.href.split('#')[0];
          var signature = ""; //config 
          var configData = {
              noncestr : config.nonceStr,
              timestamp: config.timeStamp,
              url : url
          }
          submitData.data = configData ;
          _self.A.post("/v1/common/configSign",submitData).then(function(result){
              if(result.data.state.code == 0){
                  signature = result.data.data.sign ;
                  submitData.data = orderPay ;
                  return _self.A.post("/v1/orders/moment-reward",submitData)
              }else{
                  alert(result.data.state.msg);
              }
          })
          .then(function(result){
              if(result.data.state.code == 0){
                  config.package += result.data.data.prepayId ;
                  prepayId = result.data.data.prepayId ;
                  submitData.data = config ;
                  return _self.A.post("/v1/common/paySign",submitData)
              }else{
                  console.log(result.data.state.msg);
                  _self.$emit("modalAlert",result.data.state.msg,"");
              }
          })
          .then(function(result){  // 
              if(result.data.state.code == 0){
                  var sign = result.data.data.sign ;
                  config.signType = "MD5";
                  config.paySign = sign ;
                  readyToPay.call(_self,config,signature,orderPay,prepayId);
              }
          })
      });
     }

    },

    mounted(){
      //,,;
      if (this.rewardData.hasSelectMoneyArr) {
        this.viewControl.selectMoneyArr = this.rewardData.selectMoneyArr;
      } else {
        this.viewControl.selectMoneyArr =  [2, 5, 10, 50, 100, 200];
      }

      var _self = this ;
      if(this.momentId){

      }

      var self = this;
      setTimeout(function () {
        self.showPopup = true;
      }, 0);

    },
    directives: {
      TransferDom
    },
    components: {
      Popup,Alert
    },
  }



