  //  var urlParam = {
  //    id: 0,
  //    type: 'attendance'
  //  }

  import Icon from 'vux/src/components/icon/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
;

  export default {
    components: {
      Icon,
      Alert
    },
    directives: {
      TransferDom
    },
    //:
    //@params:
    // imageData: image [ {id: 123, url: 'xxxxxxx'} ]     
    // isNeedBaseUrl: boolean                             
    props: ['imageData'],
    data() {
      return {
        baseUrl: M.baseImgUrl,
        localIds: [], //["weixin://resourceid/123123","weixin://resourceid/456456"]
        serverIds: [],
        imageObj: [],
        isDoWechatConfig: false,
        doing: '',
        imageCounter: 0,
        showMoreClick: false,
        isClicked: false,
//        multiBoxName: '',

        //iPhone
        iPhoneUrlError: false,      //,pathurl
        iPhoneUrlErrorTwice: false, //,
        iPhoneUrl: '',
        iPhoneUrlList: [],
        iPhoneUrlListIndex: 0,

        //
        alertData: {
          showAlert: false,
          alertContent: '6'
        },

        //
        urlParam: {
          id: 0,
          type: 'attendance'
        }

      }
    },
    methods: {
      proxyClick(message, event){
        console.log(event);
        // &&input
        if(!this.isChecked && event.target.className == "uploadBtn" && event.target.nodeName.toUpperCase() == "INPUT"){
          this.doClickBtn();
        }
      },
      //BaseUrl
      doFilterImageUrl(val){
        if(this.imageData.isNeedBaseUrl){
          return this.baseUrl + val;
        }
        return val;
      },
      //
      delImage(index) {
        this.imageData.image.splice(index, 1);
        this.localIds.splice(index, 1);
        this.imageCounter -= 1;
      },
      //,
      doClickBtn(){
        this.doing = "clicked" + new Date().getTime();

        if(this.imageCounter >= 6) {
          this.alertData.alertContent = "6";
          this.alertData.showAlert = true;
          return;
        }

//        this.showLoading();
        this.isClicked = true;

        var _self = this;
        _self.H.toAjax(true).then(function (data) {
          console.log("=============multiImageUpload wechat====================");
          var config = {
//            debug: true,
            appId: "wx58f3e6eb9f9a1938",
            timestamp: (parseInt((new Date().getTime()) / 1000)).toString(),
            nonceStr: M.F.getId(16),
          };
          if (M.isQC || M.isDev) {
            config.appId = M.appId
          }

          var url = window.location.href.split('#')[0];
          if(_self.iPhoneUrlError && _self.iPhoneUrl != '') {
            url = _self.iPhoneUrl;
          }

          var configData = {
            noncestr: config.nonceStr,
            timestamp: config.timestamp,
            url: url
          };

          var submitData = data;
          submitData.data = configData;

          //wechat
          _self.A.post("/v1/common/configSign", submitData).then(function (result) {
            if(result.data.state.code == 0) {
              config.signature = result.data.data.sign;
              return config;
            } else {
              return false;
            }


          }).then(function (result) {
            if(!result) {
//              _self.hideLoading();
              _self.isClicked = false;
              _self.alertData.alertContent = "~~";
              _self.alertData.showAlert = true;
            }
            var promise = function () {
              return new Promise(function (resolve, reject) {
                var config = result;
                config.jsApiList = [
                  'chooseImage',
                  'uploadImage',
                  'startRecord',
                  'stopRecord',
                  'onVoiceRecordEnd',
                  'playVoice',
                  'pauseVoice',
                  'stopVoice',
                  'onVoicePlayEnd',
                  'uploadVoice',
                  'downloadVoice',
                ];

                _self.$wechat.config({
//                  debug: true,
                  appId: config.appId,
                  timestamp: config.timestamp,
                  nonceStr: config.nonceStr,
                  signature: config.signature,
                  jsApiList: config.jsApiList
                });

                _self.$wechat.error(function (res) {
                });

                resolve();
              });
            }

            promise().then(function (userCodeResult) {
              var nowImageNums = _self.imageData.image.length;
              _self.$wechat.ready(function () {
                _self.$wechat.chooseImage({
                  count: 6 - nowImageNums, // 9
                  sizeType: ['compressed'], // 
                  sourceType: ['album', 'camera'], // 
                  success: function (res) {
                    var localIds = res.localIds; // IDlocalIdimgsrc

                    res.localIds.forEach(function (value, index) { //localIds
//                    _self.imageObj.push(value);
                      _self.localIds.push(value);
                    });

                    var syncUpload = function () {
                      var localId = localIds.shift();
                      _self.$wechat.uploadImage({
                        localId: localId,
                        isShowProgressTips: 1,
                        success: function (res) {
//                          _self.hideLoading();
                          _self.isClicked = false;
                          var serverId = res.serverId; // ID
//                          _self.serverIds.push(serverId);
                          _self.doing = "_" + new Date().getTime();

//                          var tempImg = new Image();
//                          tempImg.src = localId;
//                          tempImg.onload = function () {
                          var tempImgObj = {
                            localId: localId,
                            width: 0,
//                              width: tempImg.width,
                            height: 0,
//                              height: tempImg.height,
                            offsetX: 0,
                            serverId: serverId,
                            mediaId: serverId
                          }
                          _self.imageObj.push(tempImgObj);
                          //serverId
                          if (localIds.length > 0) {
                            syncUpload(localIds);
                          } else {
                            _self.localIds = [];  //localIds
                            _self.uploadToServer(_self.imageObj);
                          }
//                          }

                        },
                        fail: function (res) {
                          _self.doing = res;
//                          _self.hideLoading();
                          _self.isClicked = false;
                          _self.alertData.alertContent = ",,!";
                          _self.alertData.showAlert = true;
                        }
                      });
                    }

                    syncUpload(); //

                  },
                  fail: function (res) {
//                    if(_self.iPhoneUrlListIndex >= 6)
//                    _self.showMoreClick = true;
                    _self.doing = res;
                    //,
                    if(_self.iPhoneUrlError) {
                      _self.iPhoneUrlErrorTwice = true;
                      if(window.location.href.indexOf("admin") > -1){
                        _self.iPhoneUrlList = [
                          window.location.origin + '/admin/',
                          window.location.origin + '/admin/#/',
                          window.location.origin + '/admin/addMoment/',
                          window.location.origin + '/admin/#/addMoment/',
                          window.location.origin + '/admin/addMoment?id=' + _self.urlParam.id + "&type=" + _self.urlParam.type,
                          window.location.origin + '/admin/#/addMoment?id=' + _self.urlParam.id + "&type=" + _self.urlParam.type,
                          window.location.href.split('#')[0]
                        ];
                      } else {
                        _self.iPhoneUrlList = [
                          window.location.origin + '/user/',
                          window.location.origin + '/user/#/',
                          window.location.origin + '/user/addMoment/',
                          window.location.origin + '/user/#/addMoment/',
                          window.location.origin + '/user/addMoment?id=' + _self.urlParam.id + "&type=" + _self.urlParam.type,
                          window.location.origin + '/user/#/addMoment?id=' + _self.urlParam.id + "&type=" + _self.urlParam.type,
                          window.location.href.split('#')[0]
                        ];
                      }

                      _self.iPhoneUrl = _self.iPhoneUrlList[_self.iPhoneUrlListIndex];
                      _self.iPhoneUrlListIndex += 1;

                      if(_self.iPhoneUrlListIndex > _self.iPhoneUrlList.length){
                        _self.iPhoneUrlListIndex = _self.iPhoneUrlList.length - 1;
//                      _self.hideLoading();
                        _self.isClicked = false;

                        if(_self.showMoreClick){
                          _self.iPhoneUrlListIndex = 0;
                          _self.iPhoneUrl = '';
                          _self.iPhoneUrlList = [];
                          _self.isClicked = false;
                          _self.showMoreClick = false;
                          window.location.reload(true);
                        } else {
                          _self.alertData.alertContent = "~~";
                          _self.alertData.showAlert = true;
                        }

                        _self.showMoreClick = true;

                        return;
                      }
//                    _self.hideLoading();
                      _self.isClicked = false;
                      _self.doClickBtn();
                    } else {
                      _self.iPhoneUrlError = true;
                      _self.iPhoneUrl = M.F.D.get("pathname").split("#")[0];
//                    _self.hideLoading();
                      _self.isClicked = false;
                      _self.doClickBtn();
                    }
                  },
                  cancel: function (res) {
//                    _self.hideLoading();
                    _self.isClicked = false;
                  },
                });
              });
            });
          });
        });


      },
      //
      uploadToServer(imageObj){

        this.doing = imageObj;

        var _self = this;
        _self.H.toAjax(true).then(function (data) {
          var submitData = data;
          submitData.data = {
            images: imageObj
          }
          _self.A.post("/v1/common/uploadImageList", submitData).then(function (result) {
            console.info("===================serverId====================");
            _self.doing = result.data.data.images;
            if(result.data.data) {
              var images = result.data.data.images;
              images.forEach(function (value,index) {
                var obj = {
                  id: value.id,
                  url: value.url
                }
                _self.imageData.image.push(obj);
                _self.imageCounter += 1;
              });
            } else {
              alert("");
            }
            _self.imageObj = [];          //imageObj
            _self.iPhoneUrlError = false; //
            _self.iPhoneUrl = '';         //url
//            _self.hideLoading();          //loading
            _self.isClicked = false;
          });
        });
      },
    },
//    created(){
//      this.multiBoxName = "multiBox_" + Math.floor(Math.random()*1000) + "_" + new Date().getTime();
//      var multiBoxElement = document.querySelector("#" + multiBoxName);
//    },
    mounted() {
      this.doing = 'mounted';
      this.urlParam.id = this.$route.query.id;
      this.urlParam.type = this.$route.query.type;
//      var multiBoxElement = document.querySelector("#" + this.multiBoxName);
//      var _self = this;
//      console.log(this.multiBoxName)
//      console.log(document.querySelector("#" + this.multiBoxName))
//      multiBoxElement.addEventListener("click",function (e) {
//        console.log(e);
//        // &&input
//        if(!_self.isChecked && e.target.className == "uploadBtn" && e.target.nodeName.toUpperCase() == "INPUT"){
//          _self.doClickBtn();
//        }
//      })
    }
  }



