
  import Toast from 'vux/src/components/toast/index.vue'


  function isAndroid() {
    var u = navigator.userAgent;
    var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1;
    return isAndroid;
  }
  function isiOS() {
    var u = navigator.userAgent;
    var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
    return isiOS;
  }

  var isiOSBoolean = isiOS();
  var isAndroidBoolean = isAndroid();

  export default {
    props: ['voiceData'],//{voice: [], voiceMountLimit: Number, hideBtnWhen}
    data(){
      return {
        doing: '',
        toastData: {
          content: '',
          show: false,
          type: 'text' //success, warn, cancel, text
        },

        currentRecord: {},
//        localRecords: [{localId:1,second: 60},{localId:2,second: 2}],
        localRecords: [],
        serverRecords: [],  //{id,url,localId,serverId,mediaId}

        isRecording: false,
        recordingSecond: 0,
        recordingTimer: null,
        isPlaying: false,
        localRecordsPlayingIndex: null
      }
    },
    methods: {
      startRecord(isRecording){
        this.doing = "";

        if (this.voiceData.voiceMountLimit || this.voiceData.voiceMountLimit > 0 || this.localRecords.length > 0) {
          if (this.voiceData.voiceMountLimit <= this.localRecords.length || this.voiceData.voiceMountLimit <= this.localRecords.length) {
            //
            alert("" + this.voiceData.voiceMountLimit + "~~");
            return;
          }
        }

        var _self = this;

        //(1)
        if (isiOSBoolean || !isAndroidBoolean) {
          _self.isRecording = isRecording;
          _self.voiceData.isRecording = isRecording;
          var temp = setTimeout(function () {
            _self.$wechat.startRecord({
              success: function (res) {
//                _self.isRecording = isRecording;
//                _self.voiceData.isRecording = isRecording;
                _self.recordingTimer = setInterval(function () {
                  _self.recordingSecond += 1;
                }, 1000);
              },
              fail: function (res) {
                alert("");
                clearInterval(_self.recordingTimer);
                _self.recordingTimer = null;
                _self.recordingSecond = 0;
                _self.isRecording = false;
                _self.voiceData.isRecording = false;
              }
            });
          }, 300);
          var temp2 = setTimeout(function () {
            _self.autoStopRecord();
          }, 10000);

          //()
        } else {
          _self.isRecording = isRecording;
          _self.voiceData.isRecording = isRecording;
          var promise = new Promise(function (resolve, reject) {
            _self.$wechat.stopRecord({
              complete: function (res) {
                resolve();
              }
            });
          });
          promise.then(function () {
            var temp = setTimeout(function () {
              _self.$wechat.startRecord({
                success: function (res) {
//                  _self.isRecording = isRecording;
//                  _self.voiceData.isRecording = isRecording;
                  _self.recordingTimer = setInterval(function () {
                    _self.recordingSecond += 1;
                  }, 1000);
                },
                fail: function (res) {
                  alert("");
                  clearInterval(_self.recordingTimer);
                  _self.recordingTimer = null;
                  _self.recordingSecond = 0;
                  _self.isRecording = false;
                  _self.voiceData.isRecording = false;
                }
              });
            }, 300);
            var temp2 = setTimeout(function () {
              _self.autoStopRecord();
            }, 10000);
          });
        }
      },
      stopRecord(isRecording){
        var _self = this;

        if (_self.recordingSecond <= 2 && _self.isRecording) {
          _self.toastData.content = "";
          _self.toastData.type = "text";
          _self.toastData.show = true;
          return;
        } else {
          clearInterval(_self.recordingTimer);  //
          _self.recordingTimer = null;

          _self.$wechat.ready(function () {
            _self.$wechat.stopRecord({
              success: function (res) {
                var localId = res.localId;
                var second = _self.recordingSecond;
                _self.currentRecord = {
                  localId: localId,
                  second: second
                }
                _self.localRecords.push(_self.currentRecord);

                _self.isRecording = false;  //
                _self.voiceData.isRecording = false;
                _self.recordingSecond = 0;

                var temp = setTimeout(function () {
                  _self.uploadVoice(_self.currentRecord);  //
                }, 500);
              },
              fail: function (res) {
                _self.doing = res;
                _self.isRecording = false;  //
                _self.voiceData.isRecording = false;
                _self.recordingSecond = 0;
                _self.currentRecord = {};
              }
            });
          });

        }
      },
      autoStopRecord(){
        this.doing = "1"
        if (this.isRecording && this.recordingSecond >= 2) {
          var _self = this;
          _self.$wechat.ready(function () {
            _self.$wechat.onVoiceRecordEnd({
              //  complete 
              success: function (res) {
                clearInterval(_self.recordingTimer);
                _self.recordingTimer = null;
                var localId = res.localId;
                var second = _self.recordingSecond;
                _self.recordingSecond = 0;
                _self.currentRecord = {
                  localId: localId,
                  second: second
                }
                _self.localRecords.push(_self.currentRecord);
                _self.isRecording = false;
                _self.voiceData.isRecording = false;

                var temp = setTimeout(function () {
                  _self.uploadVoice(_self.currentRecord);  //
                }, 1000);
              },
              fail: function (res) {
                clearInterval(_self.recordingTimer);
                _self.recordingTimer = null;
                _self.recordingSecond = 0;
                alert("");
              }
            });
          });
        }
      },
      forceStopRecordAndPlay(stopType){ //0: stopRecord, 1: stopPlay
        this.doing = "";
        var _self = this;
        if(stopType == 0) {
          clearInterval(_self.recordingTimer);  //
          _self.recordingTimer = null;
          _self.isRecording = false;  //
          _self.voiceData.isRecording = false;
          _self.recordingSecond = 0;
          _self.$wechat.ready(function () {
            _self.$wechat.stopRecord();
          });
        } else if(stopType == 1) {
          this.stopVoice(this.localRecordsPlayingIndex);
        }
      },
      playVoice(index){
        if (this.isPlaying) { //,,
          return
        } else {
          var _self = this;
          _self.localRecordsPlayingIndex = index; //
          var voiceObj = _self.localRecords[index];//
          var temp = setTimeout(function () {
            _self.$wechat.playVoice({
              localId: voiceObj.localId,  // IDstopRecord
              success: function (res) {
                _self.isPlaying = true;
                _self.voiceData.isPlaying = true;
                var temp = setTimeout(function () {
                  _self.$wechat.ready(function () {
                    _self.$wechat.onVoicePlayEnd({
                      success: function (res) {
                        var localId = res.localId; // ID
                        _self.localRecordsPlayingIndex = null;
                        _self.isPlaying = false;
                        _self.voiceData.isPlaying = false;
                      }
                    });
                  });
                }, 1000);
              },
              fail: function () {
                _self.localRecordsPlayingIndex = null;
                _self.isPlaying = false;
                _self.voiceData.isPlaying = false;
              }
            });
          }, 250);
        }
      },
      stopVoice(index){
        if (!this.isPlaying) {  //,,
          return
        }

        var _self = this;
        var voiceObj = _self.localRecords[index];
        _self.voiceData.fatherDoStop = false;
        _self.$wechat.ready(function () {
          _self.$wechat.stopVoice({
            localId: voiceObj.localId,  // IDstopRecord
            success: function (res) {
              _self.isPlaying = false;
              _self.voiceData.isPlaying = false;
            },
            complete: function (res) {
              _self.isPlaying = false;
              _self.voiceData.isPlaying = false;
              _self.localRecordsPlayingIndex = null;
            }
          });
        });

      },
      uploadVoice(voiceObj){
        this.doing = "";

        var _self = this;
        _self.$wechat.ready(function () {
          _self.$wechat.uploadVoice({
            localId: voiceObj.localId, // IDstopRecord
//            isShowProgressTips: 1, // 1
            success: function (res) {
              var serverId = res.serverId; // ID
              voiceObj.serverId = serverId;
              _self.toastData.content = "...";
              _self.toastData.type = "text";
              _self.toastData.show = true;
              _self.uploadToService(voiceObj);
            },
            fail: function (res) {
              _self.toastData.content = "";
              _self.toastData.type = "warn";
              _self.toastData.show = true;
            }
          });
        });

      },
      uploadToService(voiceObj){
        this.doing = "";

        var audio = {
          mediaId: voiceObj.serverId,
          second: voiceObj.second
        };
        var audios = [];
        audios.push(audio);

        var _self = this;
        _self.H.toAjax(true).then(function (data) {
          var submitData = data;
          submitData.data = {
            audios: audios
          };
          _self.A.post("/v1/common/upload-audio-list", submitData).then(function (result) {
            if (result.status == 0) {
              _self.doing = result._data;
              console.log("result", result);
              for (var i = 0; i < result._data.audios.length; i++) {
                var serverRecordResultObj = result._data.audios[i];
                serverRecordResultObj.localId = voiceObj.localId;
                serverRecordResultObj.serverId = voiceObj.serverId;
//                _self.serverRecords.push(serverRecordResultObj);
                _self.voiceData.voice.push(serverRecordResultObj);
              }
              _self.toastData.content = "";
              _self.toastData.type = "success";
              _self.toastData.show = true;
            } else {
              _self.toastData.content = "";
              _self.toastData.type = "warn";
              _self.toastData.show = true;
            }
          });
        });
      },
      deleteVoice(index){
        if (this.isPlaying) {
          this.doing = "";
          this.stopVoice(index);
        }
        this.localRecords.splice(index, 1); //
        this.serverRecords.splice(index, 1);
        this.voiceData.voice.splice(index, 1);
      },

      //
      doWechatConfig(){
        var _self = this;
        _self.H.toAjax(true).then(function (data) {
          console.log("=============multiImageUpload wechat====================");
          var config = {
//            debug: true,
            appId: "wx58f3e6eb9f9a1938",
            timestamp: (parseInt((new Date().getTime()) / 1000)).toString(),
            nonceStr: M.F.getId(16),
          };
          if (M.isQC || M.isDev) {
            config.appId = M.appId;
          }
          var configData = {
            noncestr: config.nonceStr,
            timestamp: config.timestamp,
            url: window.location.href.split('#')[0]
          };
          var submitData = data;
          submitData.data = configData;
          //wechat
          _self.A.post("/v1/common/configSign", submitData).then(function (result) {
            if (result.data.state.code == 0) {
              config.signature = result.data.data.sign;
              return config;
            } else {
              return false;
            }
          }).then(function (result) {

            if (!result) {
              alert("config");
            }
            var config = result;
            config.jsApiList = [
              'chooseImage',
              'uploadImage',
              'startRecord',
              'stopRecord',
              'onVoiceRecordEnd',
              'playVoice',
              'pauseVoice',
              'stopVoice',
              'onVoicePlayEnd',
              'uploadVoice',
              'downloadVoice'
            ];
            _self.$wechat.config({
//              debug: true,
              appId: config.appId,
              timestamp: config.timestamp,
              nonceStr: config.nonceStr,
              signature: config.signature,
              jsApiList: config.jsApiList
            });

            _self.$wechat.error(function (res) {
              _self.doing = res;
            });

          }).then(function () {
            _self.$wechat.ready(function () {
              // JSJS2,
              _self.$wechat.checkJsApi({
                jsApiList: [
                  'chooseImage',
                  'uploadImage',
                  'startRecord',
                  'stopRecord',
                  'onVoiceRecordEnd',
                  'playVoice',
                  'pauseVoice',
                  'stopVoice',
                  'onVoicePlayEnd',
                  'uploadVoice',
                  'downloadVoice',
                ],
                complete: function (res) {
                  _self.doing = res;
                }
              });
            });
          });
        });
      }
    },
    mounted(){
      this.doWechatConfig();
    },
    beforeDestroy(){
      //
      if(this.isRecording || this.voiceData.isRecording) {
        this.forceStopRecordAndPlay(0);
      } else if (this.isPlaying || this.voiceData.isPlaying) {
        this.forceStopRecordAndPlay(1);
      }
    },
    components: {
      Toast
    }
  }



