


  var ii = new Image();
  ii.src = M.baseImgUrl + "/image/2017-05/ff7a577f-b6fb-8bf9-3fbb-34c64ee4479a.jpg" ;
  import html2canvas from  '../../../static/lib/js/html2canvas-beta5.js';
  var $ = window.jQuery = require("jquery");
  var qrcode = require("../../../static/lib/js/qrcode.min.js");

  var getShareCardDate = function (timeStamp) {
    var time = new Date(timeStamp);
    var fullYear = time.getFullYear();
    var month = time.getMonth();
    var date = time.getDate();
    var day = time.getDay();
    var day_cn = ['', '', '', '', '', '', ''];
    //return fullYear + '-' + month+1 + '-' + date + ' ' + day_cn[day];
    return fullYear + '' + (month + 1) + '' + date + ''
  };

  var CosCloud = require("../../../static/lib/js/cos-js-sdk-v4");
  var myFolderName = function () {
    var now = new Date();
    var nowYear = now.getFullYear();
    var nowMonth = now.getMonth() + 1;
    if (nowMonth < 10) {
      nowMonth = '0' + nowMonth;
    } else {
      nowMonth = String(nowMonth);
    }
    return '/image/' + nowYear + '-' + nowMonth + '/'
//    return '/image/' + nowYear + '-' + "05" + '/'
  };

  var dataURLtoBlob = function (dataurl) {
    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {type: mime});
  }
  var keepTouchMask = function(){
    M.F.D.set("hasShowTouch",true);
  }
  var checkTouchMask = function(){
    var status  = M.F.D.get("hasShowTouch");
    console.log(status,"status");
    if(status){

      return true ;
    }
    return false
  }
  import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


  export default {
    /*
     * @params
     * applyCardData: {
     imgBase64: '',
     isNeedUpload: false,
     image: [],
     isShowBtn: false,
     isShowCard: true,
     hasHistoryUrl: false,
     historyUrl: '',

     id: '',
     type: 'attendance',
     startTime: 0,
     speaker: '',
     name: '',
     runRenderMethod: false,
     userCode: ''
     }
     */
    props: ['applyCardData'],
    components: {
      Flexbox,
      FlexboxItem,
      XButton,
      XInput,
      Group,
      Actionsheet,
      XDialog,
      Confirm,
    },
    directives: {
      TransferDom
    },
    data() {
      return {
        baseUrl: M.baseImgUrl,
        isShowShareCard: false,   //
        showShareTemp: false,      //
        showTouchMask: false,      //  touch 
//        userCode: '',             //userCode
        renderImg: '',            //base64

        shareCard: {              //shareCard
          cardUserName: '',
          headImg: '',
          cardTime: '',
          creator: '',
        },

        cosData: {
          bucket: 'webstatic',
          appid: '1252911372',
          sid: '',
          skey: '',
          region: 'gz',
          myFolder: myFolderName(),
          fileName: function () {
            var guid = "";
            for (var i = 1; i <= 32; i++) {
              var n = Math.floor(Math.random() * 16.0).toString(16);
              guid += n;
              if ((i == 8) || (i == 12) || (i == 16) || (i == 20))
                guid += "-";
            }
            return guid;
          },
          periodSign: '',
          onceSign: '',
          cosObject: null,
        },
      }
    },
    mounted(){


    },
    watch: {
      'applyCardData.runRenderMethod'(val){
        if(this.renderImg.length > 0 && val) {
          this.isShowShareCard = true;
          this.applyCardData.runRenderMethod = false;
          this.hideLoading();
          return;
        }
        if(val) {
          this.shareCardMethod();
        }
      }
    },
    methods: {

      $hideTouchMask(){
        this.showTouchMask = false;
      },
      //
      shareCardMethod(){
        var _self = this;
        this.showLoading();
        this.showShareTemp = true;
        var ii = new Image();
        ii.onload = function(){


          console.log("success img");
          try{
            setTimeout(function () {
              var l = window.location;
              var userCode = _self.applyCardData.userCode;
              var qr = qrcode(8, 'M');
              var searchParams = "?id=" + _self.applyCardData.id + "&type=" + _self.applyCardData.type;
              var url = M.isDev ? "http://" + l.hostname + ":8085" + "/user/#/user/punchShow/formShareLink" + searchParams + "&applyCard=true&userCode=" + userCode :  l.origin + "/user/punchShow/formShareLink" + searchParams + "&applyCard=true&userCode=" + userCode ;

              qr.addData(url);
              qr.make();
              $("#QRCode").html(qr.createImgTag());
            }, 0)

            //(,,)
            var userId = M.user.userId;

            function getPixelRatio(context) {
              var backingStore = context.backingStorePixelRatio ||
                context.webkitBackingStorePixelRatio ||
                context.mozBackingStorePixelRatio ||
                context.msBackingStorePixelRatio ||
                context.oBackingStorePixelRatio ||
                context.backingStorePixelRatio || 1;
              return (window.devicePixelRatio || 1) / backingStore;
            }

            _self.H.toAjax(true).then(function (data) { //data 
              var submitData = data;
              submitData.data = {
                userId: userId
              };
              _self.A.post("/v1/user/get", submitData)
                .then(function (result) {
                  console.log(result);
                  if (result.data.state.code == 0) {
                    console.log(result.data.data.user);
                    var user = result.data.data.user;
                    _self.shareCard.cardUserName = user.nickName;
                    _self.shareCard.headImg = user.headImg.url;
                    _self.shareCard.cardTime = getShareCardDate(_self.applyCardData.startTime);
                    _self.shareCard.creator = _self.applyCardData.creator;
                  }
                  else {
                    alert(result.data.state.msg);
                  }
                })
                .then(function () {
                  var shareContent = $("#shareCard-mask").get(0);//  (dom  
                  var width = shareContent.offsetWidth;  // (dom 
                  var height = shareContent.offsetHeight; // (dom 
                  var offsetTop = shareContent.offsetTop;  //
                  var canvas = document.createElement('canvas');  //canvas 
                  var context = canvas.getContext('2d');
                  var scaleBy = getPixelRatio(context);  // ()
                  canvas.width = width * scaleBy;   // dom 
                  canvas.height = (height + offsetTop) * scaleBy;  // 
                  context.scale(scaleBy, scaleBy);
                  var opts = {
                    useCORS: true,
                    proxy: M.baseImgUrl,
                    //allowTaint:true,//
                    tainttest: true, //
                    scale: scaleBy, // scale 
                    canvas: canvas, // canvas
                    logging: true, //false
                    width: width, //dom 
                    height: height, //dom 
                  };
                  html2canvas(shareContent, opts).then(function (canvas) {
                    _self.showTouchMask = true ;
                    var body = document.getElementsByTagName("body");
                    body[0].appendChild(canvas);
                    console.log(canvas);
                    _self.renderImg = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream");


                    //setTimeout(function(){
                    //_self.showTouchMask = false ;
                    //},1000)
                    _self.hideLoading();

                    //
                    if(_self.applyCardData.isShowCard) {
                      _self.isShowShareCard = true;
                    }

                    _self.showShareTemp = false;
                    _self.applyCardData.hasHistoryUrl = true;
                    var img = new Image();
                    img.src = _self.renderImg;
                    img.onload = function(){
                      var doc = document.getElementById("touch-mask");
                      var doc1 = document.getElementById("shareCard");
                      doc.style.zIndex = 111111112 ;
                      doc1.style.zIndex = 111111;
                    }
                    canvas.style.display = "none";
                    var c = document.querySelector("canvas");
                    if(c)body[0].removeChild(c) ;
                  }).then(function () {
                    //,
                    if (_self.applyCardData.isNeedUpload) {
                      _self.getCos();
                      _self.uploadImageToCos();
                    } else {
                      _self.applyCardData.runRenderMethod = false;
                    }
                  });
                });
            });
          }
          catch(err){
            _self.hideLoading();
            _self.A.get("/v1/wechat/log?err="+err,{}).then(function(err){
              console.log(err);
            })
          }




        };
        ii.src = this.baseUrl + "/image/2017-05/ff7a577f-b6fb-8bf9-3fbb-34c64ee4479a.jpg" ;

      },
      //coscos
      getCos(){
        if (this.cosData.cosObject == null) {
          console.warn("===coscos===");
          var _self = this;
          var cosObject = new CosCloud({
            appid: this.cosData.appid,// APPID 
            bucket: this.cosData.bucket,//bucketName 
            region: this.cosData.region,
            getAppSign: function (callback) {
              var signCode = "";
              _self.H.toAjax(true).then(function (data) { //data 
                var submitData = data;
                _self.A.post("/v1/common/cos-sign", submitData).then(function (result) {
                  console.log(result.data.data);
                  signCode = result.data.data.periodSign;
                  callback(signCode);
                });
              });
            },
            getAppSignOnce: function (callback) {
              var signCode = "";
              _self.H.toAjax(true).then(function (data) { //data 
                var submitData = data;
                _self.A.post("/v1/common/cos-sign", submitData).then(function (result) {
                  console.log(result.data.data);
                  signCode = result.data.data.onceSign;
                  callback(signCode);
                });
              });
            }
          });

          this.cosData.cosObject = cosObject;
        }
      },
      //
      uploadImageToCos(){
        var _self = this;
        console.warn("======");

//        for (var i in this.data.tempList) {
        var fileName = _self.cosData.fileName();
        // alert(fileName);
        this.cosData.cosObject.uploadFile(
          _self.successCallBack,
          _self.errorCallBack,
          _self.progressCallBack,
          _self.cosData.bucket,
          _self.cosData.myFolder + fileName + ".jpg",
          dataURLtoBlob(_self.renderImg),
          1
        );
//        }

      },
      successCallBack: function (result) {
        console.log("==========,img url==========");
        console.log(result);

        var image = {
          url: result.data.access_url.replace(/^http:\/\/[\w\d\-\_.]+\.com/i, ""),
          width: 0,
          height: 0,
          offsetX: 0
        };

        var _self = this;
        //
        _self.H.toAjax(true).then(function (data) { //data 
          var submitData = data;
          submitData.data.image = image;
          _self.A.post("/v1/common/uploadImage", submitData).then(function (result) {

            console.log("===,imgid,urlvue.imageData.image===");
            _self.applyCardData.image = [{id: result.data.data.imageId, url: result.data.data.url}];

            _self.applyCardData.runRenderMethod = false;
          });
        });
      },
      errorCallBack: function (result) {
        console.log("====================");
      },
      progressCallBack: function (curr) {
        console.log(':' + curr * 100 + "%");
      },
    }
  }



